import React from 'react'
import bag1 from './images/bag1.jpg'
import bag2 from './images/bag2.jpg'
import giftmen1 from './images/giftmen1.jpg'
import giftmen2 from './images/giftmen2.jpg'

class Parallax extends React.Component {
    constructor() {
        super()
        this.state = {
            img2pos: 0
        }

        this.parallax = this
            .parallax
            .bind(this)

        this.elt = React.createRef()
        this.elt2 = React.createRef()
    }

    parallax() {
        const eltPosition = this.elt.current.getBoundingClientRect().top - window.innerHeight;
        console.log(eltPosition)
        if (eltPosition < 0) {
            this.setState({img2pos: eltPosition/7})
        } else {
            this.setState({img2pos: 0})
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.parallax)
    }

    render() {
        const {img2pos} = this.state
        return (
            <div className="col-25 mgbt-2 mgbt-m-6 relative">
                <div className='offset-7 col-11 offset-s-1 col-s-14 absolute'>
                    <img className="img-responsive" src={giftmen1} alt='img'/>
                </div>
                <div
                    ref={this.elt}
                    className='offset-14 col-7 offset-s-10 col-s-9 mgt-5 upper'
                    style={{
                    transform: `translateY(${img2pos}px)`
                }}>
                    <img className="img-responsive" src={giftmen2} alt='img'/>
                </div>
            </div>
        )
    }
}

export default Parallax