import React from 'react';

class Tile extends React.Component {
    constructor() {
        super()
        this.wrapper = React.createRef();
        this.reveal = this
            .reveal
            .bind(this)

    }

    reveal() {
        const node = this.wrapper.current;
        const rect = node.getBoundingClientRect();
        const dist = rect.top
        if (dist < window.innerHeight) {
            setTimeout(() => node.classList.remove('hide'), 500)
        } else {
            setTimeout(() => node.classList.add('hide'), 500)
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.reveal)

    }

    render() {
        const {img, alt, title, cta, classname} = this.props

        return (
            <div ref={this.wrapper} className={classname}>
                <div className="row portrait">
                    <img className="tileimg" src={img} alt={alt}/>
                    <div className="inner-bottom offset-1 mgbt-1 mgbt-m-2">
                        <h3 className="inner-title light mgbt-text-10">{title}</h3>
                        <a className="text-s light" href="#">{cta}</a>
                    </div>
                </div>
            </div>
        )
    }
}

export default Tile