import React from 'react';
import Navigation from './Navigation';
import video from './videos/video.mp4';
import video2 from './videos/video2.mp4';
import video3 from './videos/video3.mp4';

class Header extends React.Component {
    constructor() {
        super()

        this.el = React.createRef();

        this.fadeIn = this
            .fadeIn
            .bind(this)
        this.state = {
            video: video2,
            link: 'For him',
        }
    }

    fadeIn() {
        const node = this.el.current;
        setTimeout(() => node.classList.remove('hide'), 800)
    }

    

    componentDidMount() {
        window.addEventListener('load', this.fadeIn)
    }

    render() {
        const {link} = this.state

        return ( <> <div className="col-25 head align-end">
            <div
                className="col-14 offset-s-3 mgt-m-4 col-s-25 video-container"
                dangerouslySetInnerHTML={{
                __html: `<video class="video" autoplay loop muted playsinline> <source src=${this.state.video} alt="video" type="video/mp4"/></video>`
            }}/>
            <div
                ref={this.el}
                className="title-bloc offset-15 col-9 offset-s-1 col-s-23 flex-col hide">
                <p className="text-s light mgbt-text-30">Lacoste gift guide</p>
                <h1 className="big-title light">{this.props.title}</h1>
                <p className="text-s light">Lorem ipsum Nam Donec malesuada leo, vehicula
                    commodo Morbi tincidunt lacus aliquet ultrices tempor scelerisque neque Cras sed
                    vitae eget Morbi rhoncus feugiat ultricies.</p>
            </div>
        </div> < Navigation link = {link} /> </>)
    }
}

export default Header