import React from 'react'

class Title extends React.Component {
    constructor() {
        super()
        this.el = React.createRef();
        this.reveal = this
            .reveal
            .bind(this);

        
        this.state = {
            classname: "offset-6 col-13 offset-s-1 col-s-23 flex-col",
            toggle: true
        }
    }

    reveal() {
        const node = this.el.current;
        const rect = node.getBoundingClientRect();
        const dist = rect.top
        if (dist < window.innerHeight) {
            setTimeout(() => node.classList.remove('hide'), 500)
        } else {
            setTimeout(() => node.classList.add('hide'), 500)
        }
    }



    componentDidMount() {
        window.addEventListener('scroll', this.reveal)
    }

    render() {
        const {title, text} = this.props;
        const {classname} = this.state;
        return (
            <div ref={this.el} className="col-25 mgbt-2" >
                <div className={classname}>
                    <h1 className="middle-title text-custom dark mgbt-text-10">{title}</h1>
                    <p className="text-s dark">{text}</p>
                </div>
            </div>
        )
    }
}

export default Title